import React from 'react'
import Slider from "react-rangeslider";
import './style.scss'
const ReactSlider = ({range, handleChangeVertical, formatPc}) => {
    const horizontalLabels = {
        0: "0",
        5: "|",
        10: "10",
        15: "|",
        20: "20",
        25: "|",
        30: "30",
        35: "|",
        40: "40",
        45: "|",
        50: "50",
        55: "|",
        60: "60",
        65: "|",
        70: "70",
        75: "|",
        80: "80",
        85: "|",
        90: "90",
        95: "|",
        100: "100",
      };


  return (
    <>
        <div className="px-3 slider">
          <Slider
            min={0}
            max={100}
            value={range}
            labels={horizontalLabels}
            format={formatPc}
            onChange={handleChangeVertical}
          />
          {/* <div className="value">{formatPc(range)}</div> */}
        </div>
    </>
  ) 
}

export default ReactSlider
