import { all, put, takeLatest } from "redux-saga/effects";
import { FETCH_PROFILE } from "Redux/Actions/ActionType";
import { startLoader, stopLoader } from "Redux/Actions/Loader";
import { setProfileData } from "Redux/Actions/Profile";
import { API } from "Services/Api/Constants";
import { getRequest } from "Shared/Axios";

function* fetchProfile({payload}) {
    try {
      yield put(startLoader())
      const {data, status} = yield getRequest({
        API: API.PROFILE,
        DATA: payload?.formData
      });
  
      if(status===200){
        if (payload?.success) {
          // payload.success(data?.data);
        }
        console.log(data?.data)
        yield put(setProfileData(data?.data))
      }
    
    } catch (error) {
      console.log(error);
      if (payload?.fail) {
        payload?.fail(error?.data?.message);
      }
    }
    finally{
      yield put(stopLoader())
    }
  }

function* profile() {
    yield all([
      takeLatest(FETCH_PROFILE, fetchProfile), 
    ]);
  }
  
  export default profile;