import React from "react";

const MembershipTypeCell = ({ data, memberShipUpdate, currentValue }) => {
  const handleChange = (event) => {
    memberShipUpdate(data.value);
  };

  return (
    <div className="col-md mb-md-0 mb-2">
      <div className="form-check custom-option custom-option-icon checked">
        <label className="form-check-label custom-option-content" htmlFor="customRadioPercentage">
          <span className="custom-option-body">
            <svg
              width={40}
              height={40}
              viewBox="0 0 40 40"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path d={data.path1} fill="currentColor" fillOpacity="0.2" />
              <path d={data.path2} fill="currentColor" />
            </svg>
            <span className="custom-option-title">{data.title}</span>
            <small>{data.message}</small>
          </span>
          <input
            name="customRadioIcon"
            className="form-check-input"
            type="radio"
            value={data.title}
            onChange={handleChange}
            checked={data.value === currentValue}
            id="customRadioPercentage"
          />
        </label>
      </div>
    </div>
  );
};
export default MembershipTypeCell;
