import { takeLatest, all, put } from "redux-saga/effects";

import {
  FORGET_PASSWORD,
  LOGIN,
  LOGOUT,
  RESET_PASSWORD,
  setAuthToken,
  SIGN_UP,
} from "../Actions/Auth";
// import { login, logout } from "Services/Api/Auth";
// import requestSaga from "Shared/RequestSaga";
import { API } from "Services/Api/Constants";
import { postRequest, updateAuthToken } from "Shared/Axios";
import { startLoader, stopLoader } from "Redux/Actions/Loader";

function* login({ payload }) {
  try {
    yield put(startLoader());
    const { data, status } = yield postRequest({
      API: API.LOGIN,
      DATA: payload?.formData,
    });

    if (status === 200) {
      if (payload?.success) {
        console.log(data?.data?.token);
        updateAuthToken(data?.data?.token);
        yield put(setAuthToken(data));
        payload.success(data);
      }
    }
  } catch (error) {
    console.log(error);
    if (payload?.fail) {
      payload?.fail(error?.data?.message);
    }
  } finally {
    yield put(stopLoader());
  }
}

function* forgetPassword({ payload }) {
  try {
    yield put(startLoader());
    const { data, status } = yield postRequest({
      API: API.FORGET_PASSWORD,
      DATA: payload?.formData,
    });

    if (status === 200) {
      if (payload?.success) {
        payload.success(data?.message);
      }
    }
  } catch (error) {
    console.log(error);
    if (payload?.fail) {
      payload?.fail(error?.data?.message);
    }
  } finally {
    yield put(stopLoader());
  }
}

function* signup({ payload }) {
  try {
    yield put(startLoader());
    console.log("Pay load ", payload);
    const { data, status } = yield postRequest({
      API: API.REGISTER,
      DATA: payload.formData,
    });
    if (status === 200) {
      console.log("Data recived", data);
      payload.success(data);
    }
  } catch (error) {
    console.log("Some error :", error);
    payload.error(error);
  } finally {
    yield put(stopLoader());
  }
}

function* resetPassword({ payload }) {
  try {
    yield put(startLoader());
    const { data, status } = yield postRequest({
      API: API.RESET_PASSWORD,
      DATA: payload?.formData,
    });

    if (status === 200) {
      if (payload?.success) {
        payload.success(data?.message);
      }
    }
  } catch (error) {
    console.log(error);
    if (payload?.fail) {
      payload?.fail(error?.data?.message);
    }
  } finally {
    yield put(stopLoader());
  }
}

function* watchAuth() {
  yield all([
    takeLatest(LOGIN, login),
    takeLatest(FORGET_PASSWORD, forgetPassword),
    takeLatest(RESET_PASSWORD, resetPassword),
    takeLatest(SIGN_UP, signup),
    // takeLatest(LOGOUT, requestSaga, logout)
  ]);
}

export default watchAuth;
