import { PROFILE_DATA, SET_PROFILE_DATA } from "Redux/Actions/ActionType";

const initialState={
    data: null
}

function ProfileReducer(state=initialState,action){

    switch(action.type){
        case PROFILE_DATA:
            console.log(action?.payload)
            return {
                ...state,
                data: action?.payload
            }
        default:
            return state;
    }
}

export default ProfileReducer;