import React from "react";
import { Formik, Form } from "formik";
import * as yup from "yup";
import "./style.scss";
import TextField from "Components/atoms/TextField";
import { useHistory } from "react-router-dom";
import { ROUTE_CONSTANTS } from "Shared/Routes";
import { useDispatch } from "react-redux";
import { login } from "Redux/Actions/Auth";
import { withSnackbar } from "notistack";
import { errorSnackBar, responseBar, successSnackBar } from "Shared/Utilities";

const initialValues = {
  email: "",
  password: "",
};

const validationSchema = yup.object({
  email: yup.string().email("Enter a valid email").required("Email is required"),
  password: yup.string().required("Password is Required"),
});

const Login = ({ enqueueSnackbar }) => {
  const history = useHistory();
  const dispatch = useDispatch();

  const handleSubmit = (data) => {
    console.log(data);
    let formData = {
      email: data?.email,
      password: data?.password,
    };
    dispatch(
      login({
        formData: formData,
        success: () => {
          let msg = "Successfully Login";
          enqueueSnackbar(msg, successSnackBar);
        },
        fail: (error) => {
          let err = error ? error : "Something went wrong";
          enqueueSnackbar(err, errorSnackBar);
        },
      })
    );
  };

  return (
    <div className="d-flex align-items-center min-vh-100 py-3 login_section">
      <div className="container">
        <div className="login_screen">
          <div className="row">
            <div className="col-md-9 col-12 mx-auto">
              <div className="common_form">
                <div className="common_title text-center mb-3">
                  <h1 className="h2">Login</h1>
                </div>
                <Formik
                  onSubmit={handleSubmit}
                  initialValues={initialValues}
                  validationSchema={validationSchema}
                >
                  {(values, setFieldValue) => (
                    <Form>
                      <div className="row">
                        <div className="col-md-12 my-2">
                          <label>Email</label>
                          <TextField
                            name={"email"}
                            placeholder={"Enter you email (name@example.com)"}
                            type="email"
                          />
                        </div>
                        <div className="col-md-12 my-2">
                          <label>Password</label>
                          <TextField
                            name={"password"}
                            placeholder={"Enter your password"}
                            type="password"
                          />
                        </div>
                        <div className="form-group text-end">
                          <a
                            className="pointer"
                            onClick={() =>
                              history.push({ pathname: ROUTE_CONSTANTS.FORGOT_PASSWORD })
                            }
                          >
                            Forgot Password?
                          </a>
                        </div>
                        <div className="col-12 my-3 text-center common_btn">
                          <button type="submit" className="btn btn-lg btn-primary text-capitalize">
                            <span>Login</span>
                          </button>
                        </div>
                        <div className="col-12 common_text">
                          <p className="text-center pt-3 mb-0">
                            Don't have an account?
                            <a
                              className="link_txt text-reset"
                              onClick={() => history.push({ pathname: ROUTE_CONSTANTS.SIGN_UP })}
                            >
                              {" "}
                              Sign up
                            </a>
                          </p>
                        </div>
                      </div>
                    </Form>
                  )}
                </Formik>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default withSnackbar(Login);
