import { LOGIN } from "../Actions/Auth";
import { ACTION_STATES } from "../Actions/ActionStates";
import { SET_AUTH_TOKEN } from "Redux/Actions/ActionType";

const initialState = {
  token: null,
  profile: null,
};
const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_AUTH_TOKEN: {
      return {
        ...state,
        token: action?.payload?.data?.token ? action?.payload?.data?.token : null,
        profile: action?.payload?.data?.user ? action?.payload?.data?.user : null,
      };
    }
    case LOGIN + ACTION_STATES.SUCCESS: {
      return {
        ...state,
        token: action.token,
      };
    }
    default: {
      return state;
    }
  }
};

export default authReducer;
