
import React, { useState } from "react";
import { useDispatch } from "react-redux";
// import { Images } from '../../../Shared/Images';
import TextField from "Components/atoms/TextField";
import * as yup from "yup";
import { useHistory, useLocation, useParams } from "react-router-dom";
import { withSnackbar } from "notistack";
import { Form, Formik } from "formik";
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import './style.scss'
import { resetPassword } from "Redux/Actions/Auth";
import { errorSnackBar, successSnackBar } from "Shared/Utilities";

const validationHandler = yup.object({
    password: yup
        .string()
        .max(25, "Maximum character count exceeded")
        .min(6, "Password must contain atleast 6 characters")
        .required("Password is Required")
        .matches(
            /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[+!@#$%^&*])(?=.{8,})/,
            "Password must contain atleast one uppercase, one lowercase , one number and one special character."
        ),
    confirmPassword: yup
        .string()
        .max(25, "Maximum character count exceeded")
        .oneOf([yup.ref("password"), null], "Password does not match.")
        .required("password is required"),
});

const ResetPassword = ({enqueueSnackbar}) => {
	const dispatch = useDispatch();
	const [password, setPassword] = useState(false);
	const [confirmPassword, setConfirmPassword] = useState(false);
	const params = useParams();
	const history = useHistory();

	const submitHandler = (values) => {
		let data = {
			uid: params.id,
			token: params.token,
			password: values.password
		}
		console.log(data, values)
		dispatch(resetPassword({
			formData: data,
			success: (msg)=>{
                let message = msg? msg :"Please login"
                history.push("/login")
				enqueueSnackbar(message, successSnackBar);
			},
			fail: (error)=>{
				let err = error? error : 'Something went wrong'
				enqueueSnackbar(err, errorSnackBar);
			}
		}))
	}

	return (
		<>

			<div className="d-flex align-items-center min-vh-100 py-3 login_section">
			<div className='container'>
				<div className='login_screen'>
					<div className='row'>
						<div className='col-md-9 col-12 mx-auto'>
							<div className='common_form'>
								<div className='common_title text-center mb-3'>
									<h1 className='h2'>Reset Password</h1>
								</div>
										<Formik
											onSubmit={submitHandler}
											initialValues={{
												password: "",
												confirmPassword: "",

											}}
											validationSchema={validationHandler}
										>
                                            {(values, setFieldValue) => (
										<Form>
											<div className="row">
												<div className="form-group col-md-12 pwd-label password-view position-relative ">
													<TextField
														name={"password"}
														placeholder={"Password"}
														type={!password ? "password" : "text"}
													/>
													<div className="pwd-view-img" onClick={() => { setPassword(!password) }}>
														{password ? (
                                                            <VisibilityIcon />
                                                        ) :(
                                                            <VisibilityOffIcon />
                                                        )}
													</div>
												</div>

												<div className="form-group col-md-12 pwd-label password-view position-relative" >
													<TextField
														name={"confirmPassword"}
														placeholder={"Confirm Password"}
														type={!confirmPassword ? "password" : "text"}
													/>
													<div className="pwd-view-img" onClick={() => { setConfirmPassword(!confirmPassword) }}>
														{confirmPassword ? (
                                                            <VisibilityIcon />
                                                        ) :(
                                                            <VisibilityOffIcon />
                                                        )}
													</div>
												</div>


											</div>
											<div className="col-md-12 d-flex forgot-btn form-group two_btns">
												<button type="submit" className="btn btn-primary mx-2 " role="button">Continue</button>
												<button type="button" className="btn btn-secondary mx-2" role="button" onClick={()=>history.push("/login")}>Cancel</button>
											</div>
										</Form>
									)}
                                    </Formik>
                                </div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</>
	);
};

export default withSnackbar(ResetPassword);
